@import url(https://fonts.googleapis.com/css?family-Roboto+Slab: 100, 300, 400, 700);
@import url(https://fonts.googleapis.com/css?family=Raleway: 300, 3001,400,4001, 500, 5001, 600,600,);
/* edited part for cards display */

* {
  box-sizing: border-box;
  padding: auto;
  margin: auto;
  background-color: #d2dbdd;
}

.heading_style {
  padding: 30px 0;
  text-align: center;
  font-family: "Raleway", sans-serif;
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 2px;
  font-weight: 500;
  color: #868686;
  background-color: #fff;
  font-size: 1.5rem;
  margin-bottom: 30px;
}

.cards {
  width: 100%;
  height: 100%;
}

.card {
  /* margin: 25px; */
  margin: 6%;
  padding: auto;
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0, 1);
  background-color: #fff;
  width: 20%;
  border-radius: 12px;
  box-shadow: 0px 13px 10px -7px rgba(0, 0, 0, 0.1);
  float: left;
}

.card:hover {
  box-shadow: 0px 30px 18px -8px rgba(0, 0, 0, 0.1);
  -webkit-transform: scale(1.05, 1.05);
          transform: scale(1.05, 1.05);
}

.card__img {
  width: 100%;
  height: 235px;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
}

.card__info {
  border-bottom-left-radius: 12px;
  border-bottom-right-radius: 12px;
  padding: 16px 24px 24px 24px;
  background-color: #fff;
}

.card__category {
  font-family: "Raleway", sans-serif;
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 2px;
  font-weight: 700;
  color: #0f0e0e;
  background-color: #fff;
}

.card__title {
  margin-top: 5px;
  margin-bottom: 10px;
  font-family: "Raleway", sans-serif;
  background-color: rgb(185, 174, 174);
  text-transform: capitalize;
}

button {
  padding: 5px 8px;
  font-family: "Raleway", sans-serif;
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 2px;
  font-weight: 500;
  color: #0e0606;
  background-color: rgb(199, 185, 185);
  outline: none;
  border: 1px solid black;
  cursor: pointer;
}

/* responsive css code */

/* mobile devices */

@media (max-width: 768px) {
  .heading_style {
    padding: 6px;
    text-align: center;
    font-family: "Raleway", sans-serif;
    text-transform: uppercase;
    letter-spacing: 2px;
    font-weight: 400;
    color: #0a0a0a;
    background-color: #fff;
    font-size: 1rem;
    margin-bottom: 40px;
  }

  .cards {
    width: 50%;
    height: 50%;
  }
  .card {
    /* margin: 25px; */
    margin: 8%;
    margin-left: -25%;
    margin-right: auto;
    padding: auto;
    transition: all 0.4s cubic-bezier(0.175, 0.885, 0, 1);
    background-color: #fff;
    width: 150%;
    border-radius: 25px;
    box-shadow: 0px 3px 1px -7px rgba(0, 0, 0, 0.1);
    float: left;
  }
  .card__category {
    font-family: "Raleway", sans-serif;
    text-transform: uppercase;
    font-size: 12px;
    letter-spacing: 1px;
    font-weight: 700;
    color: #0f0e0e;
    background-color: #fff;
  }
}

/* for tablet */

@media (max-width: 998px) {
  .heading_style {
    padding: 30px 0;
    text-align: center;
    font-family: "Raleway", sans-serif;
    text-transform: uppercase;
    font-size: 13px;
    letter-spacing: 2px;
    font-weight: 500;
    color: #868686;
    background-color: #fff;
    font-size: 1.5rem;
    margin-bottom: 30px;
  }

  .cards {
    width: auto;
    height: auto;
  }

  .card {
    /* margin: 25px; */
    margin: 3%;
    margin-left: 6%;
    padding: auto;
    transition: all 0.4s cubic-bezier(0.175, 0.885, 0, 1);
    background-color: #fff;
    width: 40%;
    border-radius: 5px;
    box-shadow: 0px 3px 1px -7px rgba(0, 0, 0, 0.1);
    float: left;
  }
}

